/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Fragment } from 'react';
import { Box } from '@material-ui/core';
import Layout from 'components/Layout/Base';
import BioCard from 'components/Cards/BioCard';
import SEO from 'components/SEO';
import PostList from 'components/Lists/PostList';
import RecentPostsCard from 'components/Cards/RecentPostsCard';
import CategoriesCard from 'components/Cards/CategoriesCard';
import TagsCard from 'components/Cards/TagsCard';
import Paginator from 'components/Utils/Paginator';
import { Post } from 'models/post';
import { graphql } from 'gatsby';

interface Props {
  posts?: Post[];
  title?: string;
  location: Location;
  pageInfo: {
    currentPage: number;
    pageCount: number;
    defaultUrl: string;
  };
}
const PostsLayout = ({ posts = [], title = '', location, pageInfo }: Props) => {
  return (
    <Layout
      location={location}
      left={
        <Fragment>
          <Box
            css={css`
              margin-bottom: 1rem;
            `}
          >
            <BioCard />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </Fragment>
      }
      right={
        <Fragment>
          <Box
            css={css`
              margin-bottom: 1rem;
            `}
          >
            <RecentPostsCard />
          </Box>
          <Box>
            <TagsCard />
          </Box>
        </Fragment>
      }
    >
      {title && <SEO title={title} />}
      <PostList posts={posts} />
      <Paginator pageCount={pageInfo.pageCount} currentPage={pageInfo.currentPage} defaultUrl={pageInfo.defaultUrl} />
    </Layout>
  );
};

export default PostsLayout;

export const query = graphql`
  fragment PostListQuery on MarkdownRemark {
    excerpt(pruneLength: 300)
    fields {
      slug
      path
    }
    frontmatter {
      date(formatString: "YYYY-MM-DD")
      title
      categories
      thumbnail {
        childImageSharp {
          fluid(maxWidth: 800) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  }
`;
